import React, { useState } from 'react'
import styled from 'styled-components'

import { desktopVw, mobileVw } from '../styles/utils'
import Chevron from '../assets/filters/Arrow_collection.svg'

const StyledChevron = styled(Chevron)`
  height: 4px;
  width: 6px;
  transform: ${props => (props.active ? 'rotate(180deg)' : 'rotate(0deg)')};
  cursor: pointer;
`

const ExpansionPanelContainer = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(56)} 0 ${desktopVw(40)};
  }
`

const ExpansionPanelTitleWrap = styled.div`
  border-top: 1px solid #979797;
  display: flex;
  align-items: center;
  padding: ${props =>
    props.active
      ? props.expandedText
        ? `${mobileVw(6)} 0 0 0`
        : `${mobileVw(16)} 0 ${mobileVw(3)}`
      : `${mobileVw(16)} 0`};
  justify-content: space-between;
  cursor: pointer;
  align-items: center;
  height: ${props =>
    props.expandedText && props.active ? `${mobileVw(28)}` : '100%'};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${props =>
      props.active
        ? props.expandedText
          ? `${desktopVw(6)} 0 0 0`
          : `${desktopVw(16)} 0 ${desktopVw(3)}`
        : `${desktopVw(16)} 0`};

    height: ${props =>
      props.expandedText && props.active ? `${desktopVw(28)}` : 'unset'};
  }
`

const ExpansionPanelTitle = styled.span`
  color: #000;
  font-size: ${mobileVw(8)};
  line-height: ${mobileVw(8)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(8)};
    line-height: ${desktopVw(8)};
  }
`

const ExpansionPanelChildWrapper = styled.div`
  height: var(--Height, '100%');
  display: var(--displayFlex, 'block');
  padding-bottom: ${mobileVw(16)};
  padding-top: ${mobileVw(6)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-bottom: ${desktopVw(6)};
    padding-top: ${desktopVw(6)};
  }
`

const ExpansionPanel = ({
  title,
  children,
  displayHalf,
  expandedText = false,
}) => {
  const [active, setActive] = useState(true)
  return (
    <ExpansionPanelContainer>
      <ExpansionPanelTitleWrap
        expandedText={expandedText}
        onClick={() => setActive(prevState => !prevState)}
        active={active}
      >
        <ExpansionPanelTitle>{title}</ExpansionPanelTitle>
        <StyledChevron active={active} />
      </ExpansionPanelTitleWrap>

      <ExpansionPanelChildWrapper
        style={{
          '--Height': active ? '100%' : 0,
          '--displayFlex': active ? (displayHalf ? 'flex' : 'block') : 'none',
        }}
      >
        {children}
      </ExpansionPanelChildWrapper>
    </ExpansionPanelContainer>
  )
}

export default ExpansionPanel
