const CheckboxGroup = styled.div`
  width: var(--width, '100%');
  white-space: break-spaces;
  margin-right: 4px;
`

const CapitalizeTextButton = styled(StyledTextButton)`
  text-transform: capitalize;
`

export const ProductType = ({
  onClick,
  subcollections,
  keepSubCollectionName,
  subcollectionSlug,
  filteredProducts,
}) => {
  const existingSubCollection = useMemo(() => {
    if ((filteredProducts ?? []).length === 0) {
      return subcollections
    }
    return subcollections.filter(it =>
      (it.products || []).some(i =>
        (filteredProducts ?? []).find(
          p =>
            p.product.shopifyProduct.product_id === i.shopifyProduct.product_id,
        ),
      ),
    )
  }, [filteredProducts, subcollections])
  return (
    <ExpansionPanel title='PRODUCT TYPE' expandedText>
      {existingSubCollection.map(({ id, name }) => (
        <CategoryFilterItem key={id}>
          <StyledTextButton
            onClick={() => {
              const slug = name
                .split(' ')
                .join('-')
                .toLowerCase()

              if (typeof onClick === 'function') {
                onClick(slug)
              }
              window.location.hash = slug
            }}
            asAnchor
            active={
              subcollectionSlug ===
              name
                .split(' ')
                .join('-')
                .toLowerCase()
            }
          >
            {subcollections.length === 1 && !keepSubCollectionName
              ? 'All Products'
              : name}
          </StyledTextButton>
        </CategoryFilterItem>
      ))}
    </ExpansionPanel>
  )
}

export const Featured = ({ categorySlug }) => {
  if (['women', 'men', 'home'].includes(categorySlug)) {
    return (
      <ExpansionPanel title='FEATURED' expandedText>
        <>
          <CategoryFilterItem>
            <CapitalizeTextButton to={`/${categorySlug}/new-arrivals`}>
              New Arrivals
            </CapitalizeTextButton>
          </CategoryFilterItem>
          <CategoryFilterItem>
            <CapitalizeTextButton to={`/${categorySlug}/best-sellers`}>
              Best Sellers
            </CapitalizeTextButton>
          </CategoryFilterItem>
          {/* <Divider /> */}
        </>
      </ExpansionPanel>
    )
  }
  return null
}

export const GenericFilter = React.memo(
  ({ filterValues, config, showColors }) => {
    const dispatch = useDispatch()
    const filters = useSelector(state => state.filter)

    return (
      <ExpansionPanel
        title={`${config.label} ${
          (filters[config.label] ?? []).length > 0
            ? `(${(filters[config.label] ?? []).length})`
            : ''
        }`}
        displayHalf
      >
        {filterValues.map(arrVal => (
          <CheckboxGroup
            style={{
              '--width': config?.size === 'full' ? '100%' : '50%',
            }}
          >
            {arrVal.map(it => (
              <>
                <SyledCheckboxWrapper>
                  <StyledCheckboxOption
                    id={it}
                    name={it}
                    value={it}
                    whiteColor={it.name === 'White'}
                    checkedColor={
                      showColors
                        ? it?.hex
                        : (filters[config.label] ?? []).includes(it)
                        ? '#000'
                        : '#fff'
                    }
                    showCheckmark={false}
                    onChange={() => {
                      dispatch(
                        toggleFilter(config.label, showColors ? it?.name : it),
                      )
                    }}
                    showBorder={
                      showColors
                        ? (filters[config.label] ?? []).includes(it?.name)
                        : true
                    }
                    showBordersWhenChecked={showColors}
                    selected={(filters[config.label] ?? []).includes(
                      showColors ? it?.name : it,
                    )}
                  />
                  <StyledCheckboxOptionLabel
                    onClick={() => {
                      dispatch(
                        toggleFilter(config.label, showColors ? it?.name : it),
                      )
                    }}
                  >
                    {showColors ? it.name : (it || '').split('/').join('/ ')}
                  </StyledCheckboxOptionLabel>
                </SyledCheckboxWrapper>
              </>
            ))}
          </CheckboxGroup>
        ))}
      </ExpansionPanel>
    )
  },
)
