import { useState } from 'react'
import { captureException } from '../components/Sentry/utils'
import { decodeIdFromGraphQLResponse, getPathParams } from '../utils'

const fetchShopifyDataInBatch = async productList => {
  if (productList.length === 0) {
    return {}
  }

  const productIds = productList.map(prod =>
    decodeIdFromGraphQLResponse(prod.shopifyProduct.product_id),
  )
  const queryParams = {
    productIds: productIds.join(','),
  }

  const url = `${
    process.env.GATSBY_QUINCE_STORE_FRONT_API
  }/get-shopify-product${getPathParams(queryParams)}`
  try {
    return (
      await fetch(url, {
        method: 'GET',
      })
    ).json()
  } catch (e) {
    captureException(
      e,
      { url },
      { type: 'API_FAILURE', API: 'GET_SHOPIFY_PRODUCT' },
    )
  }
}

const useFetchShopifyData = () => {
  const [shopifyData, setShopifyData] = useState({})
  const [shopifyDataLoaded, setShopifyDataLoaded] = useState(false)
  const fetchShopifyData = async productList => {
    setShopifyDataLoaded(false)
    const batchSize = 75
    const promises = []
    for (let i = 0, j = productList.length; i < j; i += batchSize) {
      const batchProductList = productList.slice(i, i + batchSize)
      promises.push(fetchShopifyDataInBatch(batchProductList))
    }
    let shopifyProductData = {}
    const promiseResults = await Promise.all(promises)
    promiseResults.forEach(p => {
      shopifyProductData = { ...shopifyProductData, ...(p?.data ? p.data : {}) }
    })
    setShopifyData(shopifyProductData)
    setShopifyDataLoaded(true)
  }

  return { fetchShopifyData, shopifyData, shopifyDataLoaded }
}

export default useFetchShopifyData
