import React, { useEffect, useState, useRef, useMemo } from 'react'
import { graphql } from 'gatsby'
import { useDispatch, useSelector } from 'react-redux'
import { useLocation, navigate } from '@reach/router'
import styled from 'styled-components'
import { clearAllFilter, setAsideSortOpen, initFilters } from '../actions'
// Components
import SEO from '../components/SEO'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import {
  decodeIdFromGraphQLResponse,
  getMetaProducts,
  getProducts,
  getPathParams,
  deepEqual,
} from '../utils'
import {
  getInitialUrlForCustomCollectionPage,
  CustomCollectionPages,
  SortLabelMap,
  getDefaultColor,
  getUpdatedSubCollectionData,
  splitProductsByColor,
  getSanitizedFilterObj,
} from '../utils/collection'
import ReferralModal from '../components/ReferralModal'
import WelcomeModal from '../components/WelcomeModal'
import useFetchPersonalizedFeed from '../hooks/useFetchPersonalizedFeed'
import { desktopVw, mobileVw } from '../styles/utils'
import NewCollection from '../components/CollectionList'

import { PageLoader } from '../components/PageLoader'
import useFetchInventoryTag from '../hooks/useFetchInventoryTag'
import theme from '../styles/theme'
import { captureException } from '../components/Sentry/utils'
import { zipObject } from '../utils/build'
import {
  fetchFiltersForCustomCollectionPage,
  fetchProductFeed,
  fetchFilteredProducts,
} from '../utils/api'
import useIntersect from '../hooks/useIntersect'
import useFetchShopifyData from '../hooks/useFetchShopifyData'
import { CollectionContext } from '../contexts'

const StyledLoader = styled(PageLoader)`
  padding: ${mobileVw(80)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(250)} 0 ${desktopVw(250)} 0;
  }
`

const Collection = ({ pageContext, data }) => {
  const dispatch = useDispatch()
  const page = data.contentfulCollection
  const productsOrderInfo = pageContext.productsOrderInfo || []
  const manualProductOrderOverride =
    pageContext.manualProductOrderOverride || []
  const referralPopupData = data.allContentfulAsset
  const welcomePopupData = data.welcomePopup.nodes.filter(
    node => node.category === 'welcomePopup',
  )[0]
  const location = useLocation()
  const subSelected = location.hash.replace('#', '')

  const {
    id: collectionId,
    categorySlug,
    categoryName,
    collectionSlug,
    collectionName,
    subcollectionSlug,
    subcollectionName,
    productReviews,
    subcollectionsContext,
    filters: pageFilters,
  } = pageContext

  const [pageSubCollections, setPageSubCollections] = useState(
    subcollectionsContext || page.subcollections,
  )

  const [filtersMasterList, setFiltersMasterList] = useState(
    pageFilters?.filterMasterList,
  )

  const { enrichProductsByColorWithInventoryTag } = useFetchInventoryTag()

  const {
    fetchShopifyData,
    shopifyData,
    shopifyDataLoaded,
  } = useFetchShopifyData()

  const [colorProductCollection, setColorProductCollection] = useState(
    pageSubCollections.reduce((acc, collection) => {
      const updatedColorsToProducts = splitProductsByColor(
        getProducts(
          categorySlug,
          collectionSlug,
          subcollectionSlug,
          collection,
        ),
        getMetaProducts(
          categorySlug,
          collectionSlug,
          subcollectionSlug,
          collection,
        ),
        true,
        collection?.showAllColors,
      )
      return { ...acc, [collection.name]: updatedColorsToProducts }
    }, {}),
  )

  const rootMargin =
    typeof window === 'undefined' ? '1600px' : `${3 * window.innerHeight}px`

  const [ref, isIntersecting] = useIntersect(null, rootMargin, 0, true)

  const [isFetching, setIsFetching] = useState(false)

  const [nextProductFeedUrl, setNextProductFeedUrl] = useState(
    getInitialUrlForCustomCollectionPage(collectionSlug),
  )

  const isCustomCollectionPage = CustomCollectionPages.includes(collectionSlug)

  let defaultColor = getDefaultColor(isCustomCollectionPage)

  const [productFeed, setProductFeed] = useState([])

  const [showLoader, setShowLoader] = useState(
    isCustomCollectionPage || collectionSlug === 'picked-for-you',
  )

  const [
    showCustomCollectionLoading,
    setShowCustomCollectionLoading,
  ] = useState()

  const didMountRef = useRef(false)
  const didMountSortRef = useRef(false)
  const firstRenderRef = useRef(false)
  const searchParamsRef = useRef()
  const filtersParamsRef = useRef({})

  const filters = useSelector(state => state.filter)
  const { sortKey } = useSelector(state => state.sort)
  const [filteredProducts, setFilteredProducts] = useState([])

  const { fetchPersonalizedFeed } = useFetchPersonalizedFeed()

  const productIds = useMemo(() => {
    return pageSubCollections.reduce((acc, currVal) => {
      ;((currVal || {}).products || []).forEach(it => {
        acc = [
          ...acc,
          decodeIdFromGraphQLResponse(it.shopifyProduct.product_id),
        ]
      })
      return acc
    }, [])
  }, [pageSubCollections])

  const pushFiltersToBrowserHistory = f => {
    const searchParams = new URLSearchParams()
    for (const i in f) {
      searchParams.set(i, f[i])
    }
    searchParamsRef.current = f
    navigate(
      searchParams.toString().length > 0 ? `?${searchParams.toString()}` : '?',
    )
    navigate(`${location.pathname}?${searchParams.toString()}`)
  }

  useEffect(() => {
    if (didMountRef.current) {
      if (!deepEqual(filtersParamsRef.current, filters)) {
        filtersParamsRef.current = filters
        fetchFilteredData()
        pushFiltersToBrowserHistory(filters)
      }
    } else {
      didMountRef.current = true
    }
  }, [
    filters.MATERIAL,
    filters.SIZE,
    filters['PRICE RANGE'],
    filters.COLOR,
    filters.FABRIC,
    filters.STYLE,
    filters.CONSTRUCTION,
  ])

  useEffect(() => {
    if (didMountSortRef.current) {
      fetchFilteredData(sortKey)
    } else {
      didMountSortRef.current = true
    }
  }, [sortKey])

  useEffect(() => {
    const urlSearchParams = new URLSearchParams(location.search)
    const params = Object.fromEntries(urlSearchParams.entries())
    const filterObj = Object.keys(params).reduce((acc, currValue) => {
      const obj = { ...acc }
      obj[currValue] = (params[currValue] ?? '').split(',')
      return obj
    }, {})
    dispatch(initFilters(getSanitizedFilterObj(filterObj)))
  }, [location.search])

  useEffect(() => {
    if (!firstRenderRef.current) {
      fetchCustomCollectionPageData(true)
      firstRenderRef.current = true
    }
    return () => {
      firstRenderRef.current = false
    }
  }, [pageContext, collectionSlug, categorySlug, subcollectionSlug])

  useEffect(() => {
    ;(async () => {
      if (
        !isFetching &&
        !showLoader &&
        !showCustomCollectionLoading &&
        !!nextProductFeedUrl &&
        isCustomCollectionPage &&
        isIntersecting &&
        productFeed.length > 0 // only fire this when we already have some data present from 1st call
      ) {
        setIsFetching(true)
        defaultColor = getDefaultColor()
        const data = await fetchProductFeed(nextProductFeedUrl)
        setIsFetching(false)
        setNextProductFeedUrl(data.next_url)
        setProductFeed(productFeed => [...productFeed, ...data.products])
        const newSubCollections = [
          {
            ...pageSubCollections[0],
            products: [...productFeed, ...data.products],
          },
          ...pageSubCollections.slice(1),
        ]
        const updatedProducts = newSubCollections.reduce((acc, val) => {
          return [...acc, ...(val.products || [])]
        }, [])
        fetchShopifyData(updatedProducts)
        handleNewSubCollections(newSubCollections)
      }
    })()
  }, [isIntersecting])

  const resetFilters = () => {
    if (isCustomCollectionPage) {
      filterSubCollectionPage(
        getInitialUrlForCustomCollectionPage(collectionSlug),
        true,
      )
    } else {
      dispatch(clearAllFilter())
      setFilteredProducts([])
    }
  }

  const fetchFilteredData = sortOrder => {
    if (Object.keys(filters).length > 0 || sortOrder) {
      window.scrollTo({
        top: 0,
        behavior: 'auto',
      })
      ;(async () => {
        const filterObj = {
          colors: filters.COLOR,
          sizes: filters.SIZE,
          prices: filters['PRICE RANGE'],
          materials: filters.MATERIAL,
          styles: filters.STYLE,
          constructions: filters.CONSTRUCTION,
          fabrics: filters.FABRIC,
          sortBy: SortLabelMap[sortKey],
        }
        if (isCustomCollectionPage) {
          const paramsStr = getPathParams(filterObj).replace('?', '&')
          const url = `${getInitialUrlForCustomCollectionPage(
            collectionSlug,
          )}${paramsStr}`
          filterSubCollectionPage(url)
        } else {
          setShowCustomCollectionLoading(true)
          const filteredData = await fetchFilteredProducts(filterObj, [
            ...new Set(productIds),
          ])
          setShowCustomCollectionLoading(false)
          setFilteredProducts(filteredData)
        }
      })()
    } else {
      resetFilters()
    }
  }

  const handleNewSubCollections = (
    newSubCollections,
    filteredProducts = false,
  ) => {
    const mq = window.matchMedia(
      `(max-width: ${theme.breakpoints.desktopSmall}px)`,
    )
    const mobileDevice = mq.matches
    const finalColorProductCollection = newSubCollections.reduce(
      (acc, collection) => {
        const updatedColorsToProducts = splitProductsByColor(
          getProducts(
            categorySlug,
            collectionSlug,
            subcollectionSlug,
            collection,
          ),
          getMetaProducts(
            categorySlug,
            collectionSlug,
            subcollectionSlug,
            collection,
          ),
          mobileDevice,
          collection?.showAllColors,
          defaultColor,
          filteredProducts,
        )
        return { ...acc, [collection.name]: updatedColorsToProducts }
      },
      {},
    )

    setColorProductCollection(finalColorProductCollection)
    const enrichProductCollection = async () => {
      const zippedProductCollection = zipObject(
        Object.keys(finalColorProductCollection),
        await Promise.all(
          Object.values(finalColorProductCollection).map(colorToProduct =>
            enrichProductsByColorWithInventoryTag(colorToProduct),
          ),
        ),
      )
      setColorProductCollection(zippedProductCollection)
    }
    const filteredSubCollections = []
    newSubCollections.forEach(subcollection => {
      if (
        (finalColorProductCollection[subcollection.name] || []).length === 0
      ) {
        // TODO: if product is live and it does not appear on collection page, then report error as fatal
        captureException(
          `${page.slug} have 0 products in ${subcollection.name} subcollection`,
        )
      } else filteredSubCollections.push(subcollection)
    })
    setPageSubCollections(filteredSubCollections)
    enrichProductCollection()
  }

  const fetchCustomCollectionPageData = async fetchFilters => {
    if (!pageContext) {
      return
    }
    // dispatch(clearAllFilter())
    // if we already has filters in query params, the use them for the first call
    let initialfilters = ''
    let filterObj = {}
    if (
      fetchFilters &&
      (isCustomCollectionPage || collectionSlug === 'picked-for-you')
    ) {
      const urlSearchParams = new URLSearchParams(location.search)
      const params = Object.fromEntries(urlSearchParams.entries())
      const filt = Object.keys(params).reduce((acc, currValue) => {
        const obj = { ...acc }
        obj[currValue] = (params[currValue] ?? '').split(',')
        return obj
      }, {})
      dispatch(initFilters(filt))
      filterObj = {
        colors: filt.COLOR,
        sizes: filt.SIZE,
        prices: filt['PRICE RANGE'],
        materials: filt.MATERIAL,
        styles: filt.STYLE,
        constructions: filt.CONSTRUCTION,
        fabrics: filt.FABRIC,
      }
      Object.keys(filterObj).forEach(
        key => filterObj[key] === undefined && delete filterObj[key],
      )
      filtersParamsRef.current = getSanitizedFilterObj(filt)
      const filterForParams = { ...filterObj, sortBy: SortLabelMap[sortKey] }
      initialfilters = getPathParams(filterForParams).replace('?', '&')
    }

    let newSubCollections = subcollectionsContext || page.subcollections
    if (collectionSlug === 'picked-for-you') {
      setShowLoader(true)
      const {
        personalizedSubCollections,
        filtersData,
      } = await fetchPersonalizedFeed()
      setFiltersMasterList(filtersData)
      newSubCollections = personalizedSubCollections
      if (Object.keys(filterObj).length > 0) {
        const filteredData = await fetchFilteredProducts(filterObj, [
          ...new Set(
            personalizedSubCollections.reduce((acc, currVal) => {
              ;((currVal || {}).products || []).forEach(it => {
                acc = [
                  ...acc,
                  decodeIdFromGraphQLResponse(it.shopifyProduct.product_id),
                ]
              })
              return acc
            }, []),
          ),
        ])
        setFilteredProducts(filteredData)
      }
      setShowLoader(false)
    } else if (isCustomCollectionPage) {
      // This is the first network call when page loads
      if (fetchFilters) {
        setShowLoader(true)
      } else {
        setShowCustomCollectionLoading(true)
      }
      const url = `${getInitialUrlForCustomCollectionPage(
        collectionSlug,
      )}${initialfilters}`
      const promiseArray = []
      promiseArray.push(fetchProductFeed(url))
      if (fetchFilters) {
        promiseArray.push(fetchFiltersForCustomCollectionPage(collectionSlug))
      }
      setTimeout(() => {
        window.scrollTo({
          top: 0,
          behavior: 'smooth',
        })
      }, 200)
      const [data, filter] = await Promise.all(promiseArray)
      if (filter) {
        setFiltersMasterList(filter)
      }
      if (fetchFilters) {
        setShowLoader(false)
      } else {
        setShowCustomCollectionLoading(false)
      }
      setNextProductFeedUrl(data.next_url)
      defaultColor = getDefaultColor(isCustomCollectionPage)
      setProductFeed(data.products)
      newSubCollections = [
        {
          name: 'All Products',
          ...newSubCollections[0],
          products: data.products,
        },
        ...newSubCollections.slice(1),
      ]
    }
    const updatedSubCollections = getUpdatedSubCollectionData(
      newSubCollections,
      isCustomCollectionPage ? [] : productsOrderInfo,
      isCustomCollectionPage ? [] : manualProductOrderOverride,
    )
    const updatedProducts = updatedSubCollections.reduce((acc, val) => {
      return [...acc, ...(val.products || [])]
    }, [])
    fetchShopifyData(updatedProducts)
    handleNewSubCollections(updatedSubCollections, !!initialfilters)
    const variantList = updatedProducts.map((p, i) => {
      const variantNode = p.shopifyProduct.variants.edges[0].node
      const productId = p.shopifyProduct.product_id
      return {
        variant_id: decodeIdFromGraphQLResponse(variantNode.id),
        product_id: decodeIdFromGraphQLResponse(productId),
        price: variantNode.price,
        index: i,
        labels: p.tags ? p.tags : [],
      }
    })
    eventTracker.track(EventType.COLLECTION_VIEW, {
      collectionId,
      collectionSlug,
      collectionName,
      categorySlug,
      categoryName,
      subcollectionSlug,
      subcollectionName,
      variantList,
    })
  }

  const filterSubCollectionPage = async (filterAndSortUrl, reset = false) => {
    if (!isFetching && !!filterAndSortUrl && isCustomCollectionPage) {
      if (reset) {
        fetchCustomCollectionPageData(false)
      } else {
        setShowCustomCollectionLoading(true)
        defaultColor = getDefaultColor()
        const url = filterAndSortUrl
        const data = await fetchProductFeed(url)
        setShowCustomCollectionLoading(false)
        setNextProductFeedUrl(data.next_url)
        setProductFeed(data.products)
        const newSubCollections = [
          {
            name: 'All Products',
            ...pageSubCollections[0],
            products: data.products,
          },
          ...pageSubCollections.slice(1),
        ]
        const updatedProducts = newSubCollections.reduce((acc, val) => {
          return [...acc, ...(val.products || [])]
        }, [])
        fetchShopifyData(updatedProducts)
        handleNewSubCollections(newSubCollections, true)
      }
    }
  }

  const seoTitle =
    categoryName !== 'Collections'
      ? `${categoryName} - ${collectionName} | Quince`
      : `${collectionName} | Quince`

  const openAsideSort = React.useCallback(() => {
    dispatch(setAsideSortOpen(true))
  }, [dispatch])

  const contextValue = React.useMemo(
    () => ({
      productReviews,
      originSlug: page.slug,
      shopifyData,
      shopifyDataLoaded,
    }),
    [productReviews, page.slug, shopifyData, shopifyDataLoaded],
  )

  if (
    !firstRenderRef.current &&
    typeof window !== 'undefined' &&
    isCustomCollectionPage
  ) {
    fetchCustomCollectionPageData(true)
    firstRenderRef.current = true
  }

  return (
    <CollectionContext.Provider value={contextValue}>
      <SEO
        title={(page.seoTitle || '').trim() || seoTitle}
        description={page.seoDescription}
        type={page.pageType}
        slug={page.slug}
        image={page.metaImage ? page.metaImage.fluid.src : null}
      />
      <WelcomeModal
        heading={welcomePopupData.heading}
        subHeading={welcomePopupData.subHeading}
        klaviyoListId={welcomePopupData.subscribeForm.klaviyoListId}
        successMessage={welcomePopupData.subscribeForm.successMessage.json}
        colorTheme={Math.round(Math.random())}
        subscriptionType='email'
      />
      <NewCollection
        page={page}
        onClick={openAsideSort}
        slug={page.slug}
        categorySlug={categorySlug}
        collectionSlug={collectionSlug}
        subcollectionSlug={subcollectionSlug}
        subcollections={pageSubCollections}
        colorProductCollection={colorProductCollection}
        subSelected={subSelected}
        showLoader={showLoader}
        isCustomCollectionPage={isCustomCollectionPage}
        dontShowName={isCustomCollectionPage}
        filtersMasterList={filtersMasterList}
        showCustomCollectionLoading={showCustomCollectionLoading}
        filteredProducts={filteredProducts}
        resetFilters={resetFilters}
      />

      {/* Separate loader for pagination */}
      {!showLoader && isFetching && <StyledLoader />}
      <div id='boundary_bottom' ref={ref} />
      <ReferralModal data={referralPopupData} />
    </CollectionContext.Provider>
  )
}

export default Collection

export const CollectionQuery = graphql`
  query Collection($id: String!) {
    contentfulCollection(id: { eq: $id }) {
      seoTitle
      seoDescription
      slug
      faq {
        id
        title
        text {
          json
        }
      }
      metaImage {
        fluid(maxWidth: 1200) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      subscribeBannerCardColor {
        name
        hexcode
      }
      name
      featuredText {
        json
      }
      filterGroups {
        id
        groupName
        filters {
          id
          name
        }
      }
      subcollections {
        id
        name
        showAllColors
        offerMessage {
          json
        }
        fillerCards {
          id
          title {
            title
          }
          subtitle
          image {
            fluid(maxWidth: 960) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          text {
            json
          }
          button {
            ...Link
          }
          cardPosition
          backgroundColor {
            ...Color
          }
        }
        products {
          id
          createdAt
          slug
          title
          subtitle
          bigThumbnailTextColor
          showAllColors
          live
          attributes {
            name
          }
          tags
          gender
          metaTags
          shippingInformation {
            shippingInformation
          }
          productImages {
            ...ProductImages
          }
          productImagesVariant {
            ...ProductImages
          }
          colors {
            name
            addBorder
            hexcode
            texture {
              description
              fluid(maxWidth: 50) {
                ...GatsbyContentfulFluid_withWebp_noBase64
              }
            }
          }
          shopifyProduct {
            ...ShopifyProduct
          }
          productPackInformation {
            packType
            packInformationList {
              shopifyId
              packQuantity
            }
          }
        }
      }
    }

    allContentfulAsset(filter: { title: { eq: "Referral Popup" } }) {
      nodes {
        description
        fixed(width: 600, height: 704) {
          ...GatsbyContentfulFixed
        }
      }
    }

    welcomePopup: allContentfulWelcomePopup {
      nodes {
        id
        heading
        subHeading
        category
        subscribeForm {
          klaviyoListId
          successMessage {
            json
          }
        }
      }
    }

    filterGroups: allContentfulFilterGroup {
      nodes {
        id
        groupName
        filters {
          name
        }
      }
    }
  }
`
