import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { desktopVw, mobileVw } from '../../styles/utils'
import { getLinkPath } from '../../utils'
import ScaledImage from '../ScaledImage'
import Button from '../Button'

const StyledFillerCard = styled.div`
  width: ${mobileVw(172)};
  height: ${mobileVw(212)};
  position: relative;
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(352)};
    height: ${desktopVw(435)};
  }
`

const BodySec = styled.div`
  align-items: center;
  color: #fff;
  text-align: center;
  flex-direction: column;
  margin: auto;
  display: flex;
  align-self: center;
  justify-content: center;
  height: 100%;
  padding: 0 20px;
`

const Text = styled.div`
  p {
    font-weight: 400;
    font-size: ${mobileVw(12)};
    line-height: ${mobileVw(18)};
    &:last-of-type {
      margin-bottom: ${mobileVw(21)};
    }
  }
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 400;
    font-size: ${mobileVw(40)};
    line-height: ${mobileVw(36)};
    &:last-of-type {
      margin-bottom: ${mobileVw(21)};
    }
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    p {
      font-size: ${desktopVw(12)};
      line-height: ${desktopVw(18)};
      &:last-of-type {
        margin-bottom: ${desktopVw(21)};
      }
    }
    h1 {
      font-size: ${desktopVw(40)};
      line-height: ${desktopVw(36)};
      &:last-of-type {
        margin-bottom: ${desktopVw(21)};
      }
    }
  }
`

const StyledButton = styled(Button)`
  color: #fff;
  border-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: ${mobileVw(140)};
  height: ${mobileVw(40)};
  &:hover {
    border-color: #fff;
    background: transparent;
  }
  span {
    font-weight: normal;
    font-size: ${mobileVw(14)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    height: ${desktopVw(40)};
    width: ${desktopVw(140)};
    span {
      font-size: ${desktopVw(14)};
    }
  }
`

const FillerCard = ({ bgImg, text, button, backgroundColor }) => {
  return (
    <StyledFillerCard bgColor={backgroundColor?.hexcode}>
      {bgImg && (
        <ScaledImage
          style={{ height: '100%', width: '100%' }}
          fluid={bgImg.fluid}
        />
      )}
      <BodySec>
        {text?.json && <Text>{documentToReactComponents(text.json)}</Text>}
        {button?.text && (
          <StyledButton to={getLinkPath(button)}>{button.text}</StyledButton>
        )}
      </BodySec>
    </StyledFillerCard>
  )
}

export default FillerCard
