import React from 'react'
import styled from 'styled-components'
import { useSpring, animated } from 'react-spring'
import { useSelector, useDispatch } from 'react-redux'
import { setAsideSortOpen, setSortBy } from '../../actions'

import { desktopVw, mobileVw } from '../../styles/utils'

import { SORT_PARAMS, SORT_PARAMS_TAGS } from '../../actions/types'

import useBodyScrollLock from '../../hooks/useBodyScrollLock'
import useOnClickOutSide from '../../hooks/useOnClickOutside'

import TextButton from '../TextButton'
import CloseIcon from '../../assets/close-black.svg'

const StyledAsideSort = styled(animated.aside)`
  z-index: 2000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  padding: 32px;
  box-sizing: border-box;
  background: ${props => props.theme.colors.secondary};
  will-change: transform;
  -ms-overflow-style: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    right: unset;
    width: ${desktopVw(440)};
    padding: ${desktopVw(30)} ${desktopVw(40)} ${desktopVw(14)};
  }
`

const CloseButton = styled.button`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const StyledCloseIcon = styled(CloseIcon)`
  height: 8px;
  width: 8px;
`

const CloseButtonDesktop = styled(TextButton)`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    text-transform: uppercase;
    color: ${props => props.theme.colors.secondary};
    font-size: ${desktopVw(10)};
    background-image: ${props =>
      `linear-gradient(${props.theme.colors.secondary}, ${props.theme.colors.secondary});`};
  }
`

const Body = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-bottom: 10px;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-bottom: ${desktopVw(10)};
  }
`

const SortGroup = styled.ul`
  /* margin-bottom: ${mobileVw(20)}; */

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(20)};
  }
`

const GroupTitle = styled.h2`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 400;
  font-size: ${mobileVw(12)};
  text-transform: uppercase;
  margin-bottom: ${mobileVw(12)};
  padding-bottom: ${mobileVw(12)};
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid ${props => props.theme.colors.greyLight};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin-bottom: ${desktopVw(15)};
    line-height: ${desktopVw(24)};
  }
`

const SortItem = styled.li`
  margin-bottom: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(1)};
  }
`

const StyledTextButton = styled(TextButton)`
  /* text-transform: uppercase; */
  font-size: ${mobileVw(14)};
  /* color: ${props => props.theme.colors.greyLight}; */
  color: ${props => (props.active ? '#000' : props.theme.colors.greyLight)};
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.secondary}, ${props.theme.colors.secondary});`};
  text-decoration: ${props => (props.active ? 'underline' : 'none')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const AsideSort = () => {
  const dispatch = useDispatch()

  const { asideSortOpen } = useSelector(state => state.ui)
  const { sortKey } = useSelector(state => state.sort)
  const [ref] = useOnClickOutSide(
    () => asideSortOpen && dispatch(setAsideSortOpen(false)),
  )

  useBodyScrollLock(asideSortOpen)

  // Animations
  const slideOutAnim = useSpring({
    transform: asideSortOpen
      ? `translate3d(0, 0, 0)`
      : ` translate3d(1000%, 0, 0)`,
  })

  return (
    <StyledAsideSort style={slideOutAnim} ref={ref}>
      <Body>
        <SortGroup>
          <GroupTitle>
            Sort
            <CloseButton
              aria-label='close sort drawer'
              onClick={() => dispatch(setAsideSortOpen(false))}
            >
              <StyledCloseIcon />
            </CloseButton>
          </GroupTitle>
          {SORT_PARAMS.map(param => (
            <SortItem key={param}>
              <StyledTextButton
                active={sortKey === param}
                onClick={() => {
                  dispatch(setSortBy(param))
                  dispatch(setAsideSortOpen(false))
                }}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'collections',
                  action: 'sort_products',
                  label: param,
                }}
              >
                {param}
              </StyledTextButton>
            </SortItem>
          ))}
        </SortGroup>
        <SortGroup>
          {SORT_PARAMS_TAGS.map(param => (
            <SortItem key={param}>
              <StyledTextButton
                active={sortKey === param}
                onClick={() => {
                  dispatch(setSortBy(param))
                  dispatch(setAsideSortOpen(false))
                }}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'collections',
                  action: 'sort_products',
                  label: param,
                }}
              >
                {param}
              </StyledTextButton>
            </SortItem>
          ))}
        </SortGroup>
      </Body>
    </StyledAsideSort>
  )
}

export default AsideSort
