import React from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { clearAllFilter, setAsideSortOpen } from '../../actions'
import FilterSvg from '../../assets/filters/Filter.svg'
import { desktopVw, mobileVw } from '../../styles/utils'
import Chevron from '../../assets/filters/Arrow_collection.svg'

const FilterMobile = styled.div`
  margin-left: ${mobileVw(10)};
  position: relative;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const FilterText = styled.span`
  font-size: ${mobileVw(8)};
  padding: 0 ${mobileVw(17)} 0 ${mobileVw(7)};
`

const ClearAllFilter = styled.span`
  font-size: ${mobileVw(8)};
  color: ${props => props.theme.colors.greyLight};
  cursor: pointer;
  border-bottom: 1px solid ${props => props.theme.colors.greyLight};
  white-space: nowrap;
  padding-bottom: ${mobileVw(2)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    line-height: ${desktopVw(13)};
    padding-bottom: ${desktopVw(3)};
  }
`

const SortWrapper = styled.div`
  font-size: ${mobileVw(8)};
  margin-right: ${mobileVw(10)};
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
`
const SortText = styled.span`
  margin-right: ${mobileVw(7)};
`

const StyledChevron = styled(Chevron)`
  height: 4px;
  width: 6px;
  transform: rotate(180deg);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(5)};
  }
`

const FilterMobileContainer = ({ setOpenFilterDrawer, filterTotalNum }) => {
  const dispatch = useDispatch()
  return (
    <FilterMobile>
      <FilterSvg
        onClick={() => {
          setOpenFilterDrawer(true)
        }}
      />
      <FilterText
        onClick={() => {
          setOpenFilterDrawer(true)
        }}
      >
        FILTER {filterTotalNum > 0 ? `(${filterTotalNum})` : ''}
      </FilterText>
      <ClearAllFilter
        onClick={() => {
          dispatch(clearAllFilter())
        }}
      >
        CLEAR ALL
      </ClearAllFilter>
      <SortWrapper onClick={() => dispatch(setAsideSortOpen(true))}>
        <SortText>SORT</SortText>
        <StyledChevron />
      </SortWrapper>
    </FilterMobile>
  )
}

export default FilterMobileContainer
