import React, { useState } from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { mobileVw } from '../styles/utils'
// Components
import Drawer from './common/Drawer'
import SubcollectionFilter from './SubCollectionFilter'
import FilterSvg from '../assets/filters/Filter.svg'
import { clearAllFilter } from '../actions/filters'

const ClearAllFilter = styled.span`
  font-size: ${mobileVw(8)};
  line-height: ${mobileVw(11)};
  cursor: pointer;
  color: ${props => props.theme.colors.greyLight};
  border-bottom: 1px solid ${props => props.theme.colors.greyLight};
`
const FilterMobile = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`
const Wrapper = styled.div`
  .side__modal__container {
    padding: ${mobileVw(29)} ${mobileVw(30)};
  }
`

const FilterText = styled.span`
  font-size: ${mobileVw(8)};
  padding: 0 ${mobileVw(17)} 0 ${mobileVw(7)};
`

const SubCollectionMobileFilter = ({
  open,
  closeModal,
  subcollections,
  categorySlug,
  collectionSlug,
  colorProductCollection,
  filterTotalNum,
  filtersMasterList,
}) => {
  const dispatch = useDispatch()
  const [visibleSection, setVisibleSection] = useState()
  return (
    <Wrapper>
      <Drawer open={open} onClose={closeModal}>
        <div>
          <FilterMobile>
            <FilterSvg />
            <FilterText>
              FILTER {filterTotalNum > 0 ? `(${filterTotalNum})` : ''}
            </FilterText>
            <ClearAllFilter
              onClick={() => {
                dispatch(clearAllFilter())
              }}
            >
              CLEAR ALL
            </ClearAllFilter>
          </FilterMobile>
          <SubcollectionFilter
            filtersMasterList={filtersMasterList}
            subcollections={subcollections}
            categorySlug={categorySlug}
            collectionSlug={collectionSlug}
            subcollectionSlug={visibleSection}
            onClick={slug => setVisibleSection(slug)}
            colorProductCollection={colorProductCollection}
          />
        </div>
      </Drawer>
    </Wrapper>
  )
}

export default SubCollectionMobileFilter
