import React from 'react'
import styled from 'styled-components'
import { useDispatch, useSelector } from 'react-redux'
import { clearAllFilter, removeFilter } from '../../actions'
import { desktopVw, mobileVw } from '../../styles/utils'
import ButtonPill from '../ButtonPill'

const StyledButtonPill = styled(ButtonPill)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    background: ${props => props.theme.colors.greyLighter};
    color: ${props => props.theme.colors.primary};
    font-family: ${props => props.theme.fonts.primary};
    margin: 0 ${desktopVw(10)} 0 0;
    padding: ${desktopVw(8)} ${desktopVw(11)};
    border-radius: ${desktopVw(32)};
    font-size: ${desktopVw(9)};
    cursor: auto;
    margin-top: ${desktopVw(10)};
  }
`

const FilterWrapper = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: center;
  }
`

const ClearAllFilter = styled.span`
  font-size: ${mobileVw(8)};
  color: ${props => props.theme.colors.greyLight};
  cursor: pointer;
  border-bottom: 1px solid ${props => props.theme.colors.greyLight};
  white-space: nowrap;
  padding-bottom: ${mobileVw(2)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    line-height: ${desktopVw(13)};
    padding-bottom: ${desktopVw(3)};
    color: ${props => props.theme.colors.primary};
  }
`

const FilterContainer = styled.section`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: ${desktopVw(26)} 0 ${desktopVw(18)};
    padding: 0 ${desktopVw(80)} 0 ${desktopVw(281)};
    min-height: ${desktopVw(27)};
  }
`

const FilterPills = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const FilterDesktopChips = () => {
  const dispatch = useDispatch()
  const filters = useSelector(state => state.filter)
  return (
    <>
      <FilterContainer>
        {Object.keys(filters).length > 0 && (
          <FilterWrapper>
            <FilterPills>
              {Object.keys(filters).map(filter =>
                filters[filter].map(filterVal => (
                  <StyledButtonPill
                    showClose
                    onClose={() => {
                      dispatch(removeFilter(filter, filterVal))
                    }}
                  >
                    <span>{filterVal}</span>
                  </StyledButtonPill>
                )),
              )}
              {Object.keys(filters).length > 0 && (
                <ClearAllFilter
                  onClick={() => {
                    dispatch(clearAllFilter())
                  }}
                >
                  Clear Filters
                </ClearAllFilter>
              )}
            </FilterPills>
          </FilterWrapper>
        )}
      </FilterContainer>
    </>
  )
}

export default FilterDesktopChips
