import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { animated, useSpring } from 'react-spring'
import useOnClickOutSide from '../../hooks/useOnClickOutside'
import { desktopVw } from '../../styles/utils'
import useBodyScrollLock from '../../hooks/useBodyScrollLock'
import { Overlay } from './Overlay'
import CloseIcon from '../../assets/close.svg'

const StyledDrawer = styled(animated.aside)`
  z-index: 2001;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  padding: 32px 46px;

  background: ${props => props.theme.colors.secondary};
  overflow-y: scroll;
  -ms-overflow-style: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    left: unset;
    width: ${desktopVw(392)};
    padding: ${desktopVw(33)} ${desktopVw(51)};
    box-shadow: ${({ isDrawerOpen }) =>
      isDrawerOpen ? `#2B2D2F 0px 0px 5px 0px` : `none`};
  }
`

const StyledCloseIcon = styled(CloseIcon)`
  path {
    fill: ${props => props.theme.colors.primary};
  }
`

const Close = styled.button`
  position: absolute;
  z-index: 1;
  top: 20px;
  right: 20px;
  width: 11px;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: ${props => (props.hide ? 'none' : 'block')};
    top: ${desktopVw(30)};
    right: ${desktopVw(30)};
    width: ${desktopVw(11)};
  }
`

function Drawer({ open, onClose, children }) {
  const [pseudoOpen, setPseudoOpen] = useState(false)

  useBodyScrollLock(open)

  useEffect(() => {
    setTimeout(() => {
      setPseudoOpen(open)
    }, 200)
  }, [open])

  const slideOutAnim = useSpring({
    transform: pseudoOpen ? `translate3d(0, 0, 0)` : ` translate3d(100%, 0, 0)`,
  })

  function handlePseudoClose() {
    setPseudoOpen(false)
    setTimeout(() => {
      onClose()
    }, 200)
  }

  const [ref] = useOnClickOutSide(() => open && handlePseudoClose())

  return (
    <>
      {open && <Overlay opacity />}
      <StyledDrawer
        ref={ref}
        isDrawerOpen={open}
        style={slideOutAnim}
        className='side__modal__container'
      >
        <Close
          className='modal__close_button'
          type='button'
          aria-label='close'
          onClick={handlePseudoClose}
        >
          <StyledCloseIcon />
        </Close>
        {children}
      </StyledDrawer>
    </>
  )
}

export default Drawer
