import React from 'react'
import ProductThumbnail from '../ProductThumbnail'

export default ({
  colorsToProduct,
  newTracker,
  isFirstSubcollection,
  isMobile,
  productReviews,
  originSlug,
  shopifyData,
  shopifyDataLoaded,
  index: i,
}) => {
  return (
    <ProductThumbnail
      title={colorsToProduct.p.title || colorsToProduct.p.shopifyProduct.title}
      contentfulProduct={colorsToProduct.p}
      staticPrice={
        colorsToProduct.p.shopifyProduct.variants.edges[0].node.price
      }
      tracker={newTracker ? `${newTracker}__4_${i}` : undefined}
      prefetchedColor={colorsToProduct.c} // this is the selected
      shouldEagerLoad={isFirstSubcollection && i < (isMobile ? 2 : 3)}
      key={`${colorsToProduct.p.id}_${colorsToProduct?.c?.name}`}
      productReviews={productReviews}
      originSlug={originSlug}
      shopifyData={shopifyData}
      shopifyDataLoaded={shopifyDataLoaded}
    />
  )
}
