import React, {
  createRef,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { desktopVw, mobileVw } from '../../styles/utils'

import { getSlugFromEnd, scrollToSection } from '../../utils'
import theme from '../../styles/theme'
import ProgressiveHydration from '../ProgressiveHydration'
import { CollectionContext } from '../../contexts'
import {
  getProductItemAndFillerCardList,
  getFilteredProductData,
} from '../../utils/collection'
import FillerCard from '../Collection/FillerCard'
import ProductThumbnailCollectionWrapper from '../Collection/ProductThumbnailCollectionWrapper'

const Name = styled.h1`
  font-family: ${props => props.theme.fonts.secondary};
  font-weight: 800;
  font-size: ${desktopVw(22)};
  line-height: ${desktopVw(22)};
`

const StyledDesktopCollectionList = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    margin-bottom: -${desktopVw(7)};
  }
`

const StyledOfferMessage = styled.section`
  /* margin-top: ${mobileVw(3)}; */
  p {
    color: #717171;
    font-size: ${mobileVw(9.5)};
    line-height: ${mobileVw(12)};
    margin: ${mobileVw(10)} 0;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    /* margin-top: ${desktopVw(3)}; */
    p {
      color: #717171;
      font-size: ${desktopVw(9.5)};
      line-height: ${desktopVw(12)};
      margin: ${desktopVw(3)} 0  ${desktopVw(28)};
    }
  }
`

const ProductItem = styled.li`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(29)} ${desktopVw(40)} 0;
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
`

const RepeatingGrid = styled.ul`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-flow: wrap;
  }
`

const RepeatingProductItem = styled(ProductItem)`
  width: ${desktopVw(352)};
  .product-thumbnail__image-wrapper {
    height: ${desktopVw(434)};
  }
`

const HeadingSec = styled.div`
  margin: ${mobileVw(10)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(3)} 0 ${desktopVw(28)};
    min-height: ${desktopVw(32)};
  }
`

const DesktopLayout = ({
  products,
  tracker,
  prefetchColor = true,
  totalSubCollection,
  offerMessage,
  subcollectionName,
  keepSubCollectionName,
  subSelected,
  dontShowName,
  isFirstSubcollection,
  filteredProducts: filteredProductsList,
  isCustomCollectionPage,
  index,
  fillerCards = [],
}) => {
  const newTracker = tracker ? `${tracker}__desktop` : undefined

  const filters = useSelector(state => state.filter)
  const [collectionListRef, setCollectionListRef] = useState({})
  const [forceHydration, setForceHydration] = useState(false)

  useEffect(() => {
    if (window && window.__client_side_navigation__) {
      setForceHydration(true)
    }
  }, [])

  const {
    productReviews = {},
    originSlug,
    shopifyData,
    shopifyDataLoaded,
  } = useContext(CollectionContext)

  useEffect(() => {
    setCollectionListRef(
      products.reduce((refObj, product) => {
        const slug = getSlugFromEnd(((product || {}).p || {}).slug)
        return {
          ...refObj,
          [slug]: createRef(),
        }
      }, {}),
    )
  }, [products])

  useEffect(() => {
    if (collectionListRef && subSelected && window.scrollY == 0) {
      scrollToCollectionSection(subSelected)
    }
  }, [collectionListRef, subSelected])

  const scrollToCollectionSection = collectionSlugParam => {
    const headerHeight =
      (parseFloat(desktopVw(theme.header.desktop.height)) * window.innerWidth) /
      100
    scrollToSection(
      collectionSlugParam,
      collectionListRef,
      0,
      headerHeight,
      'auto',
    )
  }
  const filteredProducts = useMemo(
    () =>
      getFilteredProductData(
        isCustomCollectionPage,
        filteredProductsList,
        products,
        filters,
      ),
    [filteredProductsList, products, filters],
  )
  const renderSubCollection = React.useCallback(() => {
    const productItem = getProductItemAndFillerCardList(
      filteredProducts,
      fillerCards,
    )

    return productItem.map(
      ({ type, data, colorsToProduct }, i) => {
        return (
          <>
            {type === 'Filler_Card' ? (
              <RepeatingProductItem key={data.id}>
                <FillerCard
                  subtitle={data.subtitle}
                  bgImg={data.image}
                  text={data.text}
                  button={data.button}
                  backgroundColor={data.backgroundColor}
                />
              </RepeatingProductItem>
            ) : (
              <RepeatingProductItem
                key={`${colorsToProduct.p.id}${colorsToProduct?.c?.name}`}
                ref={
                  collectionListRef[
                    getSlugFromEnd(((colorsToProduct || {}).p || {}).slug)
                  ]
                }
              >
                {!isFirstSubcollection ? (
                  <ProgressiveHydration
                    rootMargin='500px'
                    force={forceHydration}
                  >
                    <ProductThumbnailCollectionWrapper
                      colorsToProduct={colorsToProduct}
                      newTracker={newTracker}
                      isFirstSubcollection={isFirstSubcollection}
                      productReviews={productReviews}
                      originSlug={originSlug}
                      shopifyData={shopifyData}
                      shopifyDataLoaded={shopifyDataLoaded}
                      index={i}
                    />
                  </ProgressiveHydration>
                ) : (
                  <ProductThumbnailCollectionWrapper
                    colorsToProduct={colorsToProduct}
                    newTracker={newTracker}
                    isFirstSubcollection={isFirstSubcollection}
                    productReviews={productReviews}
                    originSlug={originSlug}
                    shopifyData={shopifyData}
                    shopifyDataLoaded={shopifyDataLoaded}
                    index={i}
                  />
                )}
              </RepeatingProductItem>
            )}
          </>
        )
      },
      [filteredProducts],
    )
  })
  if (filteredProducts.length === 0) return null
  return (
    <StyledDesktopCollectionList>
      <HeadingSec>
        {!dontShowName ? (
          <Name>
            {totalSubCollection === 1 && !keepSubCollectionName
              ? 'All Products'
              : subcollectionName}
          </Name>
        ) : null}
        {offerMessage && (
          <StyledOfferMessage>
            {documentToReactComponents(offerMessage.json)}
          </StyledOfferMessage>
        )}
      </HeadingSec>
      <RepeatingGrid key={index}>{renderSubCollection()}</RepeatingGrid>
    </StyledDesktopCollectionList>
  )
}

export default React.memo(DesktopLayout)
