import React, { useState } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import MobileLayout from './MobileLayout'
import Button from '../Button'
import { PageLoader } from '../PageLoader'
import {
  hasNoProductsInCustomCollection,
  hasNoProductsInStaticCollection,
} from '../../utils/collection'
import useScrollPosition from '../../hooks/useScrollPosition'
import BackToTop from '../../assets/filters/BackToTop.svg'

const StyledBackToTopIcon = styled(BackToTop)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -4px);
`

const BackToTopBtn = styled.div`
  height: 30px;
  width: 30px;
  background-color: #f4f4f4;
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 1;
`

const NoProducts = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 50px 0;
  font-size: 8px;
`

const StyledLoader = styled(PageLoader)`
  display: ${props => (props.isFetching ? 'block' : 'none')};
  padding: 250px 0 250px 0;
`
const SubCollectionW = styled.div`
  display: ${props => (!props.isFetching ? 'block' : 'none')};
`

const ResetFilters = styled(Button)`
  margin-top: 10px;
  font-size: 8px;
  padding: 10px 40px;
`

export const MobileCollection = React.memo(
  ({
    pageName,
    subcollections,
    colorProductCollection,
    prefetchColor = true,
    tracker,
    filteredProducts,
    isCustomCollectionPage,
    isFetching,
    resetFilters,
  }) => {
    const filters = useSelector(state => state.filter)
    const [showBackToTop, setShowBackToTop] = useState(false)

    useScrollPosition(() => {
      if (isCustomCollectionPage) {
        const offset = 200
        const showBtn = window.scrollY + offset > window.innerHeight
        setShowBackToTop(showBtn)
      }
    })

    const scrollToTop = () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      })
    }

    const showNoProductsButton =
      (hasNoProductsInStaticCollection(
        isCustomCollectionPage,
        filters,
        filteredProducts,
      ) ||
        hasNoProductsInCustomCollection(
          isCustomCollectionPage,
          colorProductCollection,
          subcollections,
        )) &&
      !isFetching
    return (
      <>
        <SubCollectionW isFetching={isFetching}>
          {subcollections.map((subcollection, index) => (
            <MobileLayout
              key={subcollection.name}
              products={colorProductCollection[subcollection.name] || []}
              tracker={tracker}
              offerMessage={subcollection.offerMessage}
              subcollectionName={subcollection.name}
              prefetchColor={prefetchColor}
              isFirstSubcollection={index === 0}
              filteredProducts={filteredProducts}
              isCustomCollectionPage={isCustomCollectionPage}
            />
          ))}
        </SubCollectionW>
        <StyledLoader isFetching={isFetching} />
        {showNoProductsButton && (
          <NoProducts>
            {' '}
            No Products
            <ResetFilters onClick={resetFilters}>Reset filters</ResetFilters>
          </NoProducts>
        )}
        {showBackToTop && (
          <BackToTopBtn onClick={scrollToTop}>
            <StyledBackToTopIcon />
          </BackToTopBtn>
        )}
      </>
    )
  },
)
