import React from 'react'
import styled, { keyframes } from 'styled-components'
import { desktopVw, mobileVw } from '../styles/utils'

const LoadingContainer = styled.div`
  text-align: center;
  padding: ${mobileVw(350)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(370)} 0;
  }
`

const rotate = keyframes`
 0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); }
 `

const Loader = styled.div`
  height: 24px;
  width: 24px;
  color: ${props => props.theme.colors.greyLighter};
  display: inline-block;
  position: relative;
  border: 3px solid;
  border-radius: 50%;
  border-right-color: ${props => props.theme.colors.greyLight};
  animation: ${rotate} 1s linear infinite;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(24)};
    border-width: ${desktopVw(3)};
    width: ${desktopVw(24)};
  }
`

export function PageLoader({ className }) {
  return (
    <LoadingContainer className={className}>
      <Loader />
    </LoadingContainer>
  )
}
