import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import Accordion from './Accordion'

const StyledFaq = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 28px;
  background: ${props => props.theme.colors.lightPink};

  .accordion-item {
    border-bottom: ${props => `1px solid ${props.theme.colors.primary}`};
  }

  @media (max-width: ${props => props.theme.breakpoints.tablet}px) {
    .accordion-label-container {
      padding: 0;
      margin-bottom: -5px;
    }

    .accordion-item__body {
      padding-bottom: 5px;
      &__active {
        padding-bottom: 15px;
      }
    }
  }

  .faq__heading_label {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: 36px;
    margin-bottom: 21px;
  }

  h2 {
    font-size: 14px;
    line-height: 45px;
    text-transform: none;
    max-width: unset;
    font-weight: 500;
    font-family: ${props => props.theme.fonts.primary};
  }

  p {
    font-size: 11px;
    line-height: 19px;

    &:last-child {
      margin: 0;
    }
  }

  ol:last-of-type,
  ul:last-of-type {
    margin: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(40)} 0;

    .faq__heading_label {
      font-size: ${desktopVw(60)};
      line-height: ${desktopVw(60)};
      // margin-bottom: ${desktopVw(50)};
    }

    h2 {
      font-size: ${desktopVw(24)};
      line-height: ${desktopVw(45)};
    }

    p {
      font-size: ${desktopVw(13)};
      line-height: ${desktopVw(25)};
    }
  }
`

const StyledAccordion = styled(Accordion)``

const AccordionWrapper = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    border-top: none;
    padding: 0 ${desktopVw(145)};
  }
`

const FaqSection = ({ accordion, className }) => {
  return (
    <StyledFaq className={className}>
      <h2 className='faq__heading_label'>FAQ</h2>
      {accordion && (
        <AccordionWrapper>
          <StyledAccordion items={accordion} showLabelNumber openAll />
        </AccordionWrapper>
      )}
    </StyledFaq>
  )
}

export default FaqSection
