import React from 'react'
import styled from 'styled-components'
import useOnClickOutSide from '../hooks/useOnClickOutside'
import { desktopVw } from '../styles/utils'

const StyledDropDown = styled.section`
  padding: ${desktopVw(27)} ${desktopVw(32)};
  box-shadow: ${({ isDropDownOpen }) =>
    isDropDownOpen ? `0 4px 10px 0 rgb(33 33 33 / 15%)` : `none`};
`

export const DropDown = ({ open, onClose, children }) => {
  function handleDropDownClose() {
    setTimeout(() => {
      onClose()
    }, 200)
  }

  const [ref] = useOnClickOutSide(() => open && handleDropDownClose())

  return (
    <StyledDropDown
      ref={ref}
      isDropDownOpen={open}
      className='collection__dropdown'
    >
      {children}
    </StyledDropDown>
  )
}

export default DropDown
