import React, { useContext, useState, useEffect } from 'react'
import styled from 'styled-components'

import { desktopVw, mobileVw, tabletVw } from '../styles/utils'
import { getSlugFromEnd } from '../utils'
import ProductThumbnailCollectionWrapper from './Collection/ProductThumbnailCollectionWrapper'
import ProgressiveHydration from './ProgressiveHydration'
import { CollectionContext } from '../contexts'

const StyledProductGrid = styled.div`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(92)};
    padding: 0 ${desktopVw(80)};
  }
`

const ProductItem = styled.li`
  width: calc(50% - 10px);
  margin: 0 0 25px;
  padding: 0 5px;

  .product-thumbnail__image-wrapper {
    height: ${mobileVw(212)};
    width: 100%;
  }

  @media (min-width: ${props => props.theme.breakpoints.tablet}px) {
    .product-thumbnail__image-wrapper {
      height: ${tabletVw(450)};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: calc(25% - (1 / 4 * ${desktopVw(121)}));
    margin: 0 ${desktopVw(40)} ${desktopVw(43)} 0;
    padding: 0;

    .product-thumbnail {
      &__image-wrapper {
        height: ${desktopVw(357.4)};
      }
    }
  }
`

const Products = styled.ul`
  display: flex;
  flex-flow: wrap;
  padding: 0 5px;
`

const PlaceHolderDiv = styled.div`
  padding-top: ${props => props.theme.header.mobile.height}px;
  margin-top: -${props => props.theme.header.mobile.height}px;
`

const NewProductGrid = ({
  className,
  products,
  tracker,
  isFirstSubcollection,
}) => {
  const [forceHydration, setForceHydration] = useState(false)

  useEffect(() => {
    if (window && window.__client_side_navigation__) {
      setForceHydration(true)
    }
  }, [])
  const {
    productReviews = {},
    originSlug,
    shopifyData,
    shopifyDataLoaded,
  } = useContext(CollectionContext)
  return (
    <StyledProductGrid className={`${className} product-grid`}>
      {!isFirstSubcollection ? (
        <ProgressiveHydration rootMargin='500px' force={forceHydration}>
          <Products className='product-grid__list'>
            {products.map((colorsToProduct, i) => {
              return (
                <ProductItem
                  key={`${colorsToProduct.p.id}${colorsToProduct?.c?.name}`}
                  className='product-grid__item'
                >
                  <PlaceHolderDiv
                    id={getSlugFromEnd(((colorsToProduct || {}).p || {}).slug)}
                  />
                  <ProductThumbnailCollectionWrapper
                    colorsToProduct={colorsToProduct}
                    newTracker={tracker}
                    isFirstSubcollection={isFirstSubcollection}
                    productReviews={productReviews}
                    originSlug={originSlug}
                    shopifyData={shopifyData}
                    shopifyDataLoaded={shopifyDataLoaded}
                    index={i}
                    isMobile
                  />
                </ProductItem>
              )
            })}
          </Products>
        </ProgressiveHydration>
      ) : (
        <Products className='product-grid__list'>
          {products.map((colorsToProduct, i) => {
            return (
              <ProductItem
                key={`${colorsToProduct.p.id}${colorsToProduct?.c?.name}`}
                className='product-grid__item'
              >
                <PlaceHolderDiv
                  id={getSlugFromEnd(((colorsToProduct || {}).p || {}).slug)}
                />
                <ProductThumbnailCollectionWrapper
                  colorsToProduct={colorsToProduct}
                  newTracker={tracker}
                  isFirstSubcollection={isFirstSubcollection}
                  productReviews={productReviews}
                  originSlug={originSlug}
                  shopifyData={shopifyData}
                  shopifyDataLoaded={shopifyDataLoaded}
                  index={i}
                  isMobile
                />
              </ProductItem>
            )
          })}
        </Products>
      )}
    </StyledProductGrid>
  )
}

export default NewProductGrid
