import { useState, useEffect } from 'react'
import { captureWarning } from '../components/Sentry/utils'

const isClient = typeof window !== 'undefined'

// options for intersection observer
const INTERSECTION_OBSERVER_OPTIONS = {
  root: null, // windows viewport will be used
  rootMargin: '200px 0px 0px 0px',
}

const handleIntersect = (entries, observer) => {
  entries
    .filter(entry => entry.isIntersecting)
    .forEach(entry => {
      const { target } = entry
      target.onIntersect(observer)
    })
}

const getObserver = rootMargin => {
  if (rootMargin) {
    // eslint-disable-next-line immutable/no-mutation
    INTERSECTION_OBSERVER_OPTIONS.rootMargin = `0px 0px ${rootMargin}px 0px`
  }
  if (isClient && window.IntersectionObserver) {
    // eslint-disable-next-line no-undef
    return new window.IntersectionObserver(
      handleIntersect,
      INTERSECTION_OBSERVER_OPTIONS,
    )
  }
}

export default ({ ref }) => {
  const [show, setShow] = useState(false)

  useEffect(
    function() {
      const { current } = ref || {}
      if (!current) return
      const rootMargin = current.getAttribute('rootMargin')
      try {
        const observer = getObserver(rootMargin)
        if (observer) {
          observer.observe(current)
          // eslint-disable-next-line immutable/no-mutation
          current.onIntersect = observer => {
            setShow(true)
            observer.unobserve(current)
          }
        }
      } catch (e) {
        captureWarning(`Error in useNearScreen Hook - ${e}`)
        setShow(true)
      }
    },
    [ref],
  )

  return show
}
