import React, { useState } from 'react'
import styled from 'styled-components'

import { useSelector } from 'react-redux'
import { MobileCollection } from './MobileCollection'
import { DesktopCollection } from './DesktopCollection'
import FaqSection from '../FaqSection'
import { desktopVw, mobileVw } from '../../styles/utils'
import useDetectDevices from '../../hooks/useDetectDevices'
import { getPickedForYouCollectionDisplayName } from '../../utils'
import { PageLoader } from '../PageLoader'
import SubCollectionMobileFilter from '../SubCollectionFiltersMobile'
import FilterMobileInfo from '../Collection/FilterMobileTopInfo'
import FilterDesktopChips from '../Collection/FilterDesktopChipsAndInfo'
import AsideSort from '../Collection/AsideSortMobile'

const TitleWrapper = styled.div`
  background: #f3f3f3;
  padding: 0px 0px 12px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    background: white;
    padding: unset;
  }
`
const PageTitle = styled.h1`
  padding: 24px 0 12px 0;
  font-size: ${props => (props.longText ? '35px' : '47px')};
  line-height: ${props => (props.longText ? '35px' : '47px')};
  font-family: ${props => props.theme.fonts.secondary};
  text-align: center;
  text-transform: uppercase;

  @media (max-width: 320px) {
    margin: 24px 0 12px 0;
    padding: 0;
    font-size: ${props => (props.longText ? '31px' : '48px')};
    line-height: ${props => (props.longText ? '31px' : '48px')};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(44)};
    line-height: ${desktopVw(35)};
    padding: 0;
    margin: ${desktopVw(30)} ${desktopVw(80)} ${desktopVw(26)};
  }
`

const CollectionListMobile = styled.div`
  margin-bottom: 30px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const CollectionsContainer = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: flex-start;
    margin-bottom: ${desktopVw(60)};
    margin-top: ${desktopVw(28)};
  }
`

const StyledLoader = styled(PageLoader)`
  display: ${props => (props.showLoader ? 'block' : 'none')};
  padding: ${mobileVw(250)} 0 ${mobileVw(350)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(75)} 0 ${desktopVw(225)} 0;
  }
`

const CollectionWrap = styled.div`
  display: ${props => (!props.showLoader ? 'block' : 'none')};
`

const isLongWord = text => {
  if (!text) return false
  // do not check for space-separated words
  return text.split(' ').some(word => word.length >= 9)
}

const NewCollection = ({
  categorySlug,
  collectionSlug,
  page,
  activeFilters,
  subcollectionSlug,
  subcollections,
  resetData,
  colorProductCollection = {},
  subSelected,
  showLoader,
  dontShowName,
  filtersMasterList,
  showCustomCollectionLoading,
  filteredProducts,
  isCustomCollectionPage,
  resetFilters,
}) => {
  const { devices } = useDetectDevices()
  const [openFilterDrawer, setOpenFilterDrawer] = useState(false)
  const filters = useSelector(state => state.filter)

  const totalItems = Object.values(colorProductCollection).reduce(
    (acc, products = []) => {
      return acc + products.length
    },
    0,
  )

  // eslint-disable-next-line immutable/no-let
  let pageTitle = page.name
  const { userInfo } = useSelector(state => state.login)
  if (collectionSlug === 'picked-for-you') {
    pageTitle = getPickedForYouCollectionDisplayName(userInfo, page.name)
  }

  const commonProps = {
    categorySlug,
    collectionSlug,
    subcollectionSlug,
    activeFilters,
    pageName: page.name,
    resetData,
    subcollections,
    colorProductCollection,
    tracker: `collection_${page.name.toLowerCase()}`,
    subSelected,
    dontShowName,
    totalItems,
    filtersMasterList,
    filteredProducts,
    isCustomCollectionPage,
    isFetching: showCustomCollectionLoading,
    resetFilters,
  }

  const filterTotalNum = Object.keys(filters).reduce((acc, currVal) => {
    // eslint-disable-next-line no-param-reassign
    acc += (filters[currVal] ?? []).length
    return acc
  }, 0)

  return (
    <>
      <TitleWrapper>
        <PageTitle
          longText={pageTitle.length >= 8}
          longWord={isLongWord(pageTitle)}
        >
          {pageTitle}
        </PageTitle>
        <FilterMobileInfo
          setOpenFilterDrawer={setOpenFilterDrawer}
          filterTotalNum={filterTotalNum}
        />
      </TitleWrapper>
      <FilterDesktopChips />

      {devices.mobile && (
        <SubCollectionMobileFilter
          open={openFilterDrawer}
          filtersMasterList={filtersMasterList}
          closeModal={() => setOpenFilterDrawer(false)}
          subcollections={subcollections}
          categorySlug={categorySlug}
          collectionSlug={collectionSlug}
          colorProductCollection={colorProductCollection}
          filterTotalNum={filterTotalNum}
        />
      )}

      <StyledLoader showLoader={showLoader} />

      <CollectionWrap showLoader={showLoader}>
        {devices.mobile && (
          <CollectionListMobile>
            <MobileCollection {...commonProps} />
          </CollectionListMobile>
        )}
        {devices.desktop && (
          <CollectionsContainer>
            <DesktopCollection {...commonProps} />
          </CollectionsContainer>
        )}
      </CollectionWrap>

      {devices.mobile && <AsideSort />}

      {page.faq && <FaqSection accordion={page.faq} />}
    </>
  )
}

export default NewCollection
