import React, { useMemo } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import NewProductGrid from '../NewProductGrid'
import { getHyphenatedColorName } from '../../utils'
import {
  getFilteredProduct,
  getFilteredProductData,
} from '../../utils/collection'

const StyledMobileCollectionList = styled.div`
  margin-bottom: -20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const Name = styled.h1`
  font-family: ${props => props.theme.fonts.secondary};
  /* padding: 20px 10px 0; */
  /* font-weight: 800; */
  font-size: 14px;
  line-height: 25px;
`

const StyledOfferMessage = styled.section`
  /* margin: 0px 9px 25px; */
  a {
    display: inline-block;
  }
  p {
    color: #979797;
    font-size: 9.5px;
    line-height: 12px;
  }
`
const PlaceHolderDiv = styled.div`
  padding-top: ${props => props.theme.header.mobile.height}px;
  margin-top: -${props => props.theme.header.mobile.height}px;
`

const StyledHeaderContainer = styled.div`
  padding: ${props => (props.hasOfferMessage ? '19px 10px 25px' : '19px 10px')};
`

const MobileLayout = ({
  products,
  tracker,
  metaProducts,
  subcollectionName,
  offerMessage,
  isFirstSubcollection,
  filteredProducts: filteredProductsList,
  isCustomCollectionPage,
}) => {
  const filters = useSelector(state => state.filter)

  const filteredProducts = useMemo(
    () =>
      getFilteredProductData(
        isCustomCollectionPage,
        filteredProductsList,
        products,
        filters,
      ),
    [filteredProductsList, products, filters],
  )
  if (filteredProducts.length === 0) return null
  return (
    <StyledMobileCollectionList>
      <PlaceHolderDiv id={getHyphenatedColorName(subcollectionName)} />
      <StyledHeaderContainer hasOfferMessage={offerMessage}>
        <Name>{subcollectionName}</Name>
        {offerMessage && (
          <StyledOfferMessage>
            {documentToReactComponents(offerMessage.json)}
          </StyledOfferMessage>
        )}
      </StyledHeaderContainer>
      <NewProductGrid
        products={filteredProducts}
        tracker={tracker ? `${tracker}__mobile` : undefined}
        metaProducts={metaProducts}
        isFirstSubcollection={isFirstSubcollection}
      />
    </StyledMobileCollectionList>
  )
}

export default MobileLayout
