import React, { useRef, useEffect } from 'react'
import ReactDOM from 'react-dom'
import { ThemeProvider, withTheme } from 'styled-components'
import useNearScreen from '../hooks/useNearScreen'
import fallbackTheme from '../styles/theme'
import Provider from './Provider'
import { stores } from '../../fill-config'

const EMPTY_HTML = { __html: '' }
const isServer = typeof window === 'undefined'

/**
 * This cannot be used at places where we need context or where we’re using external props
 */

const ProgressiveHydration = props => {
  const { children, theme, force } = props
  const ref = useRef(null)
  const isNearScreen = useNearScreen({ ref })
  useEffect(
    function() {
      const { current: el } = ref
      /* CLIENT:
        the element is near screen
        then we hydrate the content to get the functionality ready
      */
      if (force || (!force && isNearScreen)) {
        const action = el.hasChildNodes() ? 'hydrate' : 'render'
        const component = (
          <Provider stores={stores}>
            <ThemeProvider theme={theme || fallbackTheme}>
              {children}
            </ThemeProvider>
          </Provider>
        )
        ReactDOM[action](component, el)
      }
    },
    [children, isNearScreen, force],
  )

  /*
    SERVER: Just render the content as usual
  */
  if (isServer) {
    return <div ref={ref}>{children}</div>
  }

  // CLIENT: Avoid hydration until we say so
  return (
    <div
      ref={ref}
      suppressHydrationWarning
      dangerouslySetInnerHTML={EMPTY_HTML}
    />
  )
}

export default withTheme(ProgressiveHydration)
