import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../styles/utils'
import CloseIcon from '../assets/close.svg'

const StyledButtonPill = styled.button`
  position: relative;
  padding: 8px 16px;
  border-radius: 16px;
  background: ${props => props.theme.colors.primary};
  color: ${props => props.theme.colors.secondary};
  text-transform: uppercase;
  font-family: ${props => props.theme.fonts.primary};
  font-size: 10px;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(8)} ${desktopVw(11)};
    border-radius: ${desktopVw(32)};
    font-size: ${desktopVw(10)};
  }
`

const CloseButton = styled.button`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(8)};
    display: inline-flex;
    cursor: pointer;
  }
`

const StyledCloseIcon = styled(CloseIcon)`
  width: ${desktopVw(5)};
  height: ${desktopVw(5)};
  path {
    fill: ${props => props.theme.colors.primary};
  }
`

const ButtonPill = ({ children, onClose, showClose = false, ...rest }) => {
  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <StyledButtonPill className='btn__pill_wrapper' {...rest}>
      {children}
      {showClose && (
        <CloseButton onClick={onClose}>
          <StyledCloseIcon />
        </CloseButton>
      )}
    </StyledButtonPill>
  )
}

export default ButtonPill
