import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../styles/utils'
import { ProductType, GenericFilter, Featured } from './Filters/Common'

const filters = [
  'colors',
  'sizes',
  'materials',
  'constructions',
  'styles',
  'prices',
  'fabrics',
]

const StyledSubcollectionFilter = styled.div`
  z-index: 1;
  top: ${props => props.theme.header.mobile.height}px;
  background: ${props => props.theme.colors.secondary};
  display: flex;
  align-items: center;
  margin: ${mobileVw(8)} 0 ${mobileVw(10)};
  color: #686868;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: sticky;
    border-top: 1px solid ${props => props.theme.colors.greyLighter};
    border-bottom: 1px solid ${props => props.theme.colors.greyLighter};
    position: static;
    border: none;
    margin: 0;
    margin-bottom: ${desktopVw(12)};
    flex-basis: ${desktopVw(281)};
    max-width: ${desktopVw(281)};
  }
`

const FilterList = styled.ul`
  flex: 1;
  display: flex;
  white-space: pre-wrap;
  overflow-x: scroll;
  flex-direction: column;
  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(15)};
    width: 100%;
    padding: 0;
    flex: none;
    font-size: ${desktopVw(14)};
    line-height: ${desktopVw(40)};
    color: #686868;
    overflow: hidden;
    overflow-y: scroll;
    height: ${props =>
      `calc(
        100vh - ${desktopVw(props.theme.header.desktop.height)})`};
  }
`
const ProductsCount = styled.span`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    font-size: ${desktopVw(9.5)};
    color: ${props => props.theme.colors.greyLight};
    text-transform: uppercase;
    margin: 0 ${desktopVw(56)} 0 ${desktopVw(40)};
  }
`

const DivideInto2 = list => {
  const firstHalf = []
  const secondHalf = []
  list.forEach((f, idx) => {
    if (idx % 2 === 0) {
      firstHalf.push(f)
    } else {
      secondHalf.push(f)
    }
  })
  return [firstHalf, secondHalf]
}

const SubcollectionFilter = ({
  className,
  subcollections,
  onClick,
  keepSubCollectionName,
  colorProductCollection = {},
  filtersMasterList,
  categorySlug,
  subcollectionSlug,
  filteredProducts,
}) => {
  const totalItems = Object.values(colorProductCollection).reduce(
    (acc, products = []) => {
      return acc + products.length
    },
    0,
  )
  return (
    <StyledSubcollectionFilter className={className}>
      <FilterList>
        <ProductsCount>
          {totalItems !== 1 ? `${totalItems} items` : `${totalItems} item`}
        </ProductsCount>
        {subcollections.length > 1 && (
          <ProductType
            onClick={onClick}
            subcollections={subcollections}
            keepSubCollectionName={keepSubCollectionName}
            subcollectionSlug={subcollectionSlug}
            filteredProducts={filteredProducts}
          />
        )}
        {filters
          .filter(it => (filtersMasterList?.[it]?.values ?? []).length > 0)
          .map(it => {
            return (
              <GenericFilter
                filterValues={
                  filtersMasterList?.[it]?.config?.size === 'full'
                    ? [filtersMasterList?.[it]?.values ?? []]
                    : DivideInto2(filtersMasterList?.[it]?.values ?? [])
                }
                config={filtersMasterList?.[it]?.config}
                showColors={it === 'colors'}
              />
            )
          })}
        <Featured categorySlug={categorySlug} />
      </FilterList>
    </StyledSubcollectionFilter>
  )
}

export default SubcollectionFilter
