import React, { useState } from 'react'
import styled from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'
import { setSortBy } from '../../actions'
import { desktopVw } from '../../styles/utils'
import { SORT_PARAMS, SORT_PARAMS_TAGS } from '../../actions/types'

import TextButton from '../TextButton'
import DropDown from '../DropDown'
import Chevron from '../../assets/filters/Arrow_collection.svg'

const StyledChevron = styled(Chevron)`
  height: 4px;
  width: 6px;
  transform: rotate(180deg);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(5)};
  }
`

const SortGroup = styled.ul`
  margin-bottom: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(11)};
  }
`

const StyledDropDown = styled.div`
  .collection__dropdown {
    opacity: 1;
    z-index: 2;
    position: absolute;
    background: white;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      width: ${desktopVw(172)};
      left: -${desktopVw(172)};
      min-height: ${desktopVw(316)};
      box-sizing: border-box;
    }
  }
`

const GroupTitle = styled.h2`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  margin-bottom: 23px;
  line-height: 28px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(8)};
    margin-bottom: ${desktopVw(21)};
    line-height: ${desktopVw(8)};
    cursor: default;
  }
`

const SortItem = styled.li`
  margin-bottom: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(11)};
  }
`

const StyledTextButton = styled(TextButton)`
  text-transform: capitalize;
  font-size: 15px;
  color: ${props => (props.active ? '#000' : props.theme.colors.greyLight)};
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.secondary}, ${props.theme.colors.secondary});`};
  text-decoration: ${props => (props.active ? 'underline' : 'none')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(9.5)};
    line-height: ${desktopVw(12)};
    text-align: left;
  }
`

const StyledSortText = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(8)};
    position: absolute;
    right: ${desktopVw(30)};
    top: ${desktopVw(25)};
  }
`

const SortText = styled.span`
  cursor: pointer;
`

const CollectionSortBy = () => {
  const [pseudoOpen, setPseudoOpen] = useState(false)
  const dispatch = useDispatch()
  const { sortKey } = useSelector(state => state.sort)

  const selectSort = param => {
    dispatch(setSortBy(param))
    setPseudoOpen(false)
  }
  return (
    <>
      <StyledSortText>
        <SortText role='button' onClick={() => setPseudoOpen(true)}>
          SORT
        </SortText>
        <StyledChevron onClick={() => setPseudoOpen(true)} />
        <div>
          {pseudoOpen && (
            <StyledDropDown>
              <DropDown
                onClose={() => {
                  setPseudoOpen(false)
                }}
                open={pseudoOpen}
              >
                <SortGroup>
                  <GroupTitle>
                    Sort <StyledChevron />
                  </GroupTitle>
                  {SORT_PARAMS.map(param => (
                    <SortItem key={param}>
                      <StyledTextButton
                        active={sortKey === param}
                        onClick={() => selectSort(param)}
                        trackParams={{
                          trackGA: true,
                          trackInternal: true,
                          category: 'collections',
                          action: 'sort_products',
                          label: param,
                        }}
                      >
                        {param}
                      </StyledTextButton>
                    </SortItem>
                  ))}
                </SortGroup>
                <SortGroup>
                  {SORT_PARAMS_TAGS.map(param => (
                    <SortItem key={param}>
                      <StyledTextButton
                        active={sortKey === param}
                        onClick={() => selectSort(param)}
                        trackParams={{
                          trackGA: true,
                          trackInternal: true,
                          category: 'collections',
                          action: 'sort_products',
                          label: param,
                        }}
                      >
                        {param}
                      </StyledTextButton>
                    </SortItem>
                  ))}
                </SortGroup>
              </DropDown>
            </StyledDropDown>
          )}
        </div>
      </StyledSortText>
    </>
  )
}

export default CollectionSortBy
