import React, { createRef, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import Button from '../Button'
import theme from '../../styles/theme'
import {
  getDimensions,
  getHyphenatedColorName,
  scrollToSection,
} from '../../utils'
import {
  hasNoProductsInCustomCollection,
  hasNoProductsInStaticCollection,
} from '../../utils/collection'
import SubCollectionFilterDesktop from '../SubCollectionFilter'
import { desktopVw } from '../../styles/utils'
import useScrollPosition from '../../hooks/useScrollPosition'
import DesktopLayout from './DesktopLayout'
import CollectionSortBy from '../Collection/CollectionSortBy'
import { PageLoader } from '../PageLoader'
import BackToTop from '../../assets/filters/BackToTop.svg'

const StyledBackToTopIcon = styled(BackToTop)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -4px);
`

const BackToTopBtn = styled.div`
  height: 30px;
  width: 30px;
  background-color: #f4f4f4;
  position: fixed;
  right: 30px;
  bottom: 10px;
  z-index: 1;
  cursor: pointer;
`

const StyledLoader = styled(PageLoader)`
  display: ${props => (props.isFetching ? 'block' : 'none')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(250)} 0 ${desktopVw(250)} 0;
  }
`

const SubcollectionFilterDesktop = styled(SubCollectionFilterDesktop)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: sticky;
    top: ${props => desktopVw(props.theme.header.desktop.height)};
    display: flex;
    justify-content: center;
    margin-top: ${desktopVw(24)};
  }
`

const Flex = styled.div`
  flex: 1;
  position: relative;
`

const NoProducts = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 50px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(200)} 0;
  }
`

const ResetFilters = styled(Button)`
  margin-top: 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(20)};
  }
`

const SubCollectionW = styled.div`
  display: ${props => (!props.isFetching ? 'block' : 'none')};
`

const CollectionWrapper = styled.div``

export const DesktopCollection = React.memo(
  ({
    categorySlug,
    collectionSlug,
    subcollections,
    isCustomCollectionPage,
    resetFilters,
    colorProductCollection,
    tracker,
    subSelected,
    dontShowName,
    filtersMasterList,
    filteredProducts,
    isFetching,
    fillersCards = [],
  }) => {
    const [showBackToTop, setShowBackToTop] = useState(false)
    const filters = useSelector(state => state.filter)
    const [collectionListRef, setCollectionListRef] = useState({})
    const [visibleSection, setVisibleSection] = useState(
      subcollections &&
        subcollections.length > 0 &&
        subcollections[0].name &&
        subcollections[0].name
          .split(' ')
          .join('-')
          .toLowerCase(),
    )

    useScrollPosition(() => {
      if (!isCustomCollectionPage) {
        const offset = 200
        const scrollPosition = window.scrollY + offset

        const selected = Object.keys(collectionListRef).find(slug => {
          const ele = collectionListRef[slug].current
          if (ele) {
            const { offsetBottom, offsetTop } = getDimensions(ele)
            return scrollPosition > offsetTop && scrollPosition < offsetBottom
          }
        })

        setVisibleSection(selected)
      }
    })

    useScrollPosition(() => {
      if (isCustomCollectionPage) {
        const showBtn = window.scrollY > window.innerHeight
        setShowBackToTop(showBtn)
      }
    })

    useEffect(() => {
      setCollectionListRef(() => {
        const subcollectionsRefs = subcollections.reduce(
          (refObj, collection) => {
            const hypenatedName = getHyphenatedColorName(collection.name)
            return {
              ...refObj,
              [hypenatedName]: createRef(),
            }
          },
          {},
        )
        return subcollectionsRefs
      })
    }, [subcollections])

    useEffect(() => {
      if (collectionListRef && subSelected && window.scrollY == 0) {
        scrollToCollectionSection(subSelected, 15, 'auto')
      }
    }, [collectionListRef, subSelected])

    const scrollToCollectionSection = (
      collectionSlugParam,
      offset,
      animation,
    ) => {
      const headerHeight =
        (parseFloat(desktopVw(theme.header.desktop.height)) *
          window.innerWidth) /
        100
      scrollToSection(
        collectionSlugParam,
        collectionListRef,
        offset,
        headerHeight,
        animation,
      )
    }

    const scrollToTop = () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      })
    }

    const showNoProductsButton =
      (hasNoProductsInStaticCollection(
        isCustomCollectionPage,
        filters,
        filteredProducts,
      ) ||
        hasNoProductsInCustomCollection(
          isCustomCollectionPage,
          colorProductCollection,
          subcollections,
        )) &&
      !isFetching

    const renderSubCollection = React.useCallback(() => {
      return subcollections.map((subcollection, index) => (
        <CollectionWrapper
          key={subcollection.name}
          ref={collectionListRef[getHyphenatedColorName(subcollection.name)]}
        >
          <DesktopLayout
            products={colorProductCollection[subcollection.name] || []}
            tracker={tracker}
            subcollectionName={subcollection.name}
            offerMessage={subcollection.offerMessage}
            totalSubCollection={subcollections.length}
            subSelected={subSelected}
            dontShowName={dontShowName}
            isFirstSubcollection={index === 0}
            index={index}
            filteredProducts={filteredProducts}
            isCustomCollectionPage={isCustomCollectionPage}
            fillerCards={subcollection.fillerCards}
          />
        </CollectionWrapper>
      ))
    }, [subcollections, filteredProducts, collectionListRef])

    return (
      <React.Fragment key={categorySlug}>
        <SubcollectionFilterDesktop
          filtersMasterList={filtersMasterList}
          subcollections={subcollections}
          categorySlug={categorySlug}
          collectionSlug={collectionSlug}
          subcollectionSlug={visibleSection}
          onClick={slug => scrollToCollectionSection(slug, 15, 'smooth')}
          colorProductCollection={colorProductCollection}
          filteredProducts={filteredProducts}
        />
        <Flex>
          <CollectionSortBy />
          <SubCollectionW isFetching={isFetching}>
            {renderSubCollection()}
          </SubCollectionW>
          <StyledLoader isFetching={isFetching} />
          {showNoProductsButton && (
            <NoProducts>
              {' '}
              No Products
              <ResetFilters onClick={resetFilters}>Reset filters</ResetFilters>
            </NoProducts>
          )}
          {showBackToTop && (
            <BackToTopBtn onClick={scrollToTop}>
              <StyledBackToTopIcon />
            </BackToTopBtn>
          )}
        </Flex>
      </React.Fragment>
    )
  },
)
