import { getCookie } from '../utils'
import { extractProductJsonFromAPIProduct } from '../utils/build'
import { ANALYTIC_COOKIES } from '../utils/constants'

const useFetchPersonalizedFeed = () => {
  const fetchPersonalizedFeed = async () => {
    const email =
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL) ||
      ''
    const url = `${
      process.env.GATSBY_QUINCE_STORE_FRONT_API
    }/get-personalized-feed?isNew=true&email=${email.toLowerCase()}&filtersSet=color,price`
    const subCollections = await (
      await fetch(url, {
        method: 'GET',
      })
    ).json()
    const personalizedSubCollections = subCollections.data.map(s => {
      s.products = s.products.map(p =>
        extractProductJsonFromAPIProduct(p, 1440),
      )
      return s
    })

    return {
      personalizedSubCollections,
      filtersData: subCollections.filterMasterList,
    }
  }
  return { fetchPersonalizedFeed }
}

export default useFetchPersonalizedFeed
